import React from "react";
import axios from "axios";
import CalciteThemeProvider from "calcite-react/CalciteThemeProvider";
import TopNav, { TopNavTitle } from "calcite-react/TopNav";
import StoryCard from "./StoryCard";
import QASwitch from "./QASwitch";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      results: [],
      toggle: false
    };
  }

  onToggle = e => {
    //this.props.isOn = value;
    this.setState({
      toggle: e.target.checked
    });
  };

  componentDidMount() {
    axios
      .get(
        `https://www.arcgis.com/sharing/rest/search?q=type%3Astorymap%20AND%20typekeywords%3A(smstatuspublished)%20AND%20orgid%3A(NOT(dGkWQRTbdsLW3GnN%20AND%204E2kXnaVf5dNC6X0%20AND%20nzS0F0zdNLvs7nc8))&sortorder=desc&sortfield=modified&num=100&f=pjson`
      )
      .then(res => {
        let results = res.data.results;
        let total = res.data.total;
        let num = res.data.num;
        this.setState({ results, total, num });
      });
  }

  render() {
    return (
      <CalciteThemeProvider>
        <TopNav className="top-nav fade-in">
          <TopNavTitle style={{ marginLeft: "50px" }}>
            ArcGIS StoryMaps Gallery
          </TopNavTitle>
          <div style={{ marginTop: "20px" }}>
            <QASwitch onToggle={this.onToggle} />
          </div>
        </TopNav>
        <h2 style={{ marginTop: "50px", marginLeft: "250px" }}>
          Showing <strong>{this.state.num}</strong> out of {" "}
          <strong>{this.state.total}</strong>
        </h2>
        <div className="grid-container leader-1">
          <div className="column-24">
            <div className="padding-leader-1 block-group block-group-4-up tablet-block-group-3-up phone-block-group-1-up">
              {this.state.results.map(item => (
                <StoryCard
                  key={item.id}
                  src={`https://www.arcgis.com/sharing/rest/content/items/${
                    item.id
                  }/info/${item.thumbnail}`}
                  id={item.id}
                  title={item.title}
                  owner={item.owner}
                  href={item.url}
                  isToggled={this.state.toggle}
                />
              ))}
            </div>
          </div>
        </div>
      </CalciteThemeProvider>
    );
  }
}

export default App;

