import React from "react";
import Card, { CardTitle, CardContent, CardImage } from "calcite-react/Card";
import StoryButton from "./StoryButton";

class StoryCard extends React.Component {
  render() {
    const buttonHref = this.props.isToggled
      ? `https://storymaps2qa.arcgis.com/stories/${this.props.id}`
      : this.props.href;
    return (
      <Card
        className="card block trailer-1 animated-fade-in"
        key={this.props.id}
      >
        <CardImage
          className="card-image"
          src={this.props.src}
          caption={`Author:` + this.props.owner}
          alt={this.props.title}
        />
        <CardContent className="card-content">
          <CardTitle>{this.props.title}</CardTitle>
        </CardContent>
        <StoryButton href={buttonHref}>View Story</StoryButton>
      </Card>
    );
  }
}

export default StoryCard;