import React from "react";
import Checkbox from "calcite-react/Checkbox";

class QASwitch extends React.Component {
  render() {
    const { isOn } = this.props;
    return (
      <Checkbox onChange={this.props.onToggle} checked={isOn}>
        View stories in QA
      </Checkbox>
    );
  }
}

export default QASwitch;
